<template>
  <v-app id="main-view">
    <!-- <core-filter /> -->
    <core-toolbar />
    <v-content
      :style="'padding-left: 0px;'">
      <v-fade-transition mode="out-in">
        <router-view />
      </v-fade-transition>
    </v-content>
    <!-- <core-footer v-if="$route.meta.name !== 'Maps'" /> -->
    <Chatroom
      v-show="chatView"
      @snackbar="doSnackbar"/>
    <div
      id='chat-icon'
      @click="doOpenChat()"
      >
      <div
        v-if="!chatView"
        :class="{ 'active': doReadStatus }"
        class="cursor-no-hover">
        <img :src="ChatIcon">
      </div>
      <!-- <div
        v-if="!chatView"
        class="cursor-no-hover text-white mr-1 active">
        <v-icon>mdi-message-processing</v-icon>
      </div> -->
      <div
        v-if="chatView"
        class="cursor-no-hover text-white close">
        <v-icon>mdi-close</v-icon>
      </div>
    </div>
    <div class="chatLog">
      <p
      v-for="(log, l) in chatLog"
      :key="'l'+l"
      :style="'color:'+log.value">
      {{ log.text}}
    </p>
    </div>
    <core-footer />
  </v-app>
</template>

<script>
import { mapState } from 'vuex'
import Chatroom from '../components/Plugins/Chatroom.vue'
import ChatIcon from '@/assets/img/chat_icon.png'
export default {
  name: 'MainView',
  components: {
    Chatroom
  },
  metaInfo () {
    return {
      title: this.appName
    }
  },
  data: () => ({
    ChatIcon,
    chatView: false,
    chatLog: []
  }),
  computed: {
    ...mapState(['appName', 'dologinView', 'doChatView', 'doReadStatus'])
  },
  watch: {
    doChatView (val) {
      this.chatView = val
    },
    chatView (val) {
      this.$store.state.doChatView = val
    }
  },
  methods: {
    doOpenChat () {
      if (localStorage.getItem('token')) {
        this.chatView = !this.chatView
      } else {
        this.$store.state.dologinView = true
      }
    },
    doSnackbar (val) {
      this.chatLog.push({ value: val[0], text: val[1] })
    }
  }
}
</script>
<style lang='scss'>
$chat-color: #fdbe5d;
/*聊天室icon*/
#chat-icon{
  >div{
    box-shadow: 2px 1px 10px #707070;
    position: fixed;
    bottom: 10px;
    right: 20px;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    opacity: 1;
    z-index: 999;
    background-color: $chat-color;
    display: flex;
    justify-content: center;
    align-items: center;
    >i{
      font-size: 45px;
      color: #ffffff;
    }
    >img{
      width: 45px;
      height: 45px;
    }
    &.close{
      background-color: #7a7a7a;
      border-radius: 50% 0 50% 50%;
    }
    &.active{
      animation-name: chat_btn;
      animation-duration: 0.5s;
      animation-iteration-count:infinite;
      @keyframes chat_btn{
        0%{
          bottom: 10px;
          background-color: $chat-color;
        }
        50%{
          bottom: 15px;
          background-color: #fe4545;
        }
        100%{
          bottom: 10px;
          background-color: $chat-color;
        }
      }
    }
  }
}
.chatLog{
  position: fixed;
  bottom: 10px;
  left: 20px;
  z-index: 999;
}
</style>
